import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CanDeactivateGuard } from 'src/app/shared/guards/can-deactivate.guard';
import { SecurityPolicySummaryComponent } from './security-policy-summary/security-policy-summary.component';
import { SecurityPolicyComponent } from './security-policy.component';

const routes: Routes = [
  {
    path: '',
    component: SecurityPolicyComponent,
    canDeactivate: [CanDeactivateGuard]
  },
  {
    path:'summary',
    component:SecurityPolicySummaryComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SecurityPolicyRoutingModule { }
